/*  height:auto bug fix by lhq */
.mdui-img-fluid {
  //.mdui-img-fluid();
  display: block;
  width: 100%;
}
.mdui-video-fluid {
  //.mdui-img-fluid();
  display: block;
  width: 100%;
}

/*  */
.mdui-img-rounded {
  border-radius: 2PX;
}

/*  */
.mdui-img-circle {
  border-radius: 50%;
}

// embed
.mdui-video-container {
  position: relative;
  height: 0px;
  padding-bottom: 56.25%;
  overflow: hidden;

  iframe,
  object,
  embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
  }
}
